import React from 'react';
import './Popup.css';
import './TodoApp.css';


export default function Popup(props) {   
    return (
        
        props.trigger?
        <div className="popup-container">
            <div className="popup-box">
            <input type="text" className="update-section" value={props.value} placeholder="Enter the new inputs" onChange={props.handleChange}></input>
            <button className="Update" onClick={props.clickUpdate}>Update</button>
            </div>
            <div className="close-button"><i class="far fa-times-circle" onClick={props.closeButton}></i></div>
        
        </div>:""
       
    )
}
