import React from 'react';
import './TodoApp.css';
import Popup from './Popup'

export default function TodoApp(props) {
        return(
            
            <div className="todo-container">
               <Popup listitems={props.items}/>
                <div className="input-container">
                <form  onSubmit={props.handleSubmit}>
                    <h1>TodoApp</h1>
                    <input type="text" className="input-section" value={props.value} placeholder="Enter the inputs" onChange={props.handleChange}></input>    
                     </form>
                     </div>
                
                <ul>
                    {
                        
                      props.items.map((data,index)=>(
                          <li key={index}><input type="checkbox"></input>{data}
                          <div className="image-container">
                              <i id="edit" className="far fa-edit" onClick={()=>props.clickEdit(index)}></i>
                                 <i className="fas fa-trash-alt" onClick={()=>props.deleteClick(index)}>
                              </i></div></li>
                      ))
                    }
                </ul>

            </div>
     
        )
    
}
