import React, {Component } from 'react'
import Popup from './Popup';
import TodoApp from './TodoApp';

export default class Todofunc extends Component {
    constructor(){
        super()
        this.state={
            value:'',
            items:[],
            trigger:false,
            indexValue:''
        }
    }
    handleSubmit=(event)=>
    {
        event.preventDefault();
        const {value}=this.state;
        // const allItems=this.state.items;
        // allItems.push(value);
        this.setState(
            {
              items:[...this.state.items,value],
               value:''
            }
        )        
    }
    handleChange=(event)=>
    {
        this.setState({
            value:event.target.value
        })      
    }
    deleteClick=(index)=>
    {
        const {items}=this.state;
        items.splice(index,1);
        this.setState(
            {
                Items:this.state.items
            }
        )
        
    }
    clickEdit=(indexVal)=>{
        const {items}=this.state;
        items.splice(indexVal,1);
        this.setState(
            {
                trigger:true,
                Items:this.state.items
            }
        )              
    }

    clickUpdate=()=>{
        console.log("clicked");
        // const allItems=this.state.items;
        // allItems.push(value);
        const {value}=this.state;
        this.setState(
            {
              items:[...this.state.items,value],
               value:'',
               trigger:false
            }
        )    

    }
    closeButton=()=>{
        this.setState({
            trigger:false
        })
    }
    render() {
        return (
            <div>
            <TodoApp value={this.state.value} clickEdit={this.clickEdit} items={this.state.items} handleSubmit={this.handleSubmit} handleChange={this.handleChange} deleteClick={this.deleteClick}  />
            <Popup indexValue={this.state.indexValue} closeButton={this.closeButton} clickUpdate={this.clickUpdate} trigger={this.state.trigger} handleChange={this.handleChange}  value={this.state.value}/>
            </div>
        )
    }
}
